<template>
    <form
        ref="form"
        @submit="submitCredentials"
    >
        <div class="form-group mb-3">
            <label
                class="form-label"
                for="usernameInput"
                html-for="usernameInput"
            ><input
                id="usernameInput"
                v-model="username"
                class="form-control"
                type="text"
            ></label>
        </div>
        <div class="form-group mb-3">
            <label
                class="form-label"
                for="passwordInput"
                html-for="passwordInput"
            >
                <input
                    id="passwordInput"
                    v-model="password"
                    class="form-control"
                    type="password"
                >
            </label>
        </div>
        <button
            class="btn btn-primary"
            type="submit"
        >
            
        </button>
    </form>
</template>

<script>
import {mapActions, mapState} from "vuex";
import bcrypt from "bcryptjs";

export default {
    name: "AdminPage",
    data() {
        return {
            username: "",
            password: "",
        };
    },
    computed: {
        ...mapState(["isAdmin"]),
    },
    methods: {
        ...mapActions(["setIsAdmin"]),
        submitCredentials(e) {
            e.preventDefault();
            const password = this.password;
            const hash =
                "$2a$10$F7yekPuUXAEhtfWTyGKL8OQLF7XtsWdpV8a/g5mZkz1f/uydVIS3q";

            const isPasswordCorrect = bcrypt.compareSync(password, hash);

            if (isPasswordCorrect && this.username === "fyc2005") {
                this.setIsAdmin();
                this.$router.push({path: "/", replace: true});
            }
        },
    },
};
</script>
